.LandingPage__container {
    display: flex;
    flex-direction: column;
    align-items: center;

    .LandingPage__button {
        width: 340px;
        height: 200px;
        color: white;
        background: rgba(0, 0, 0, 0.8);
        border: 5px solid;
        border-color: white;
        font-size: 20px;
        font-weight: bold;
      }

    .LandingPage__button:hover {
        background: url(../../assets/sparklers-small.png);
        cursor: pointer;
    }
  }